$navBackground: #111111;
$standardBackground: #2c2c2c;
$opacityBackground: rgba(44, 44, 44, .8);
$clickHover: rgb(32, 32, 32);
$clickableBackground: rgba(17, 17, 17, .8);
$textColor: rgb(255, 255, 255);

*{
    text-decoration: none;
    margin: 0;
    padding: 0;
    font-family: "Prata", Arial, Helvetica, sans-serif;
    color: $textColor;
}

html {
    background-image: url("monotoneBusiness1080.webp");
    background-size: cover;
}

body {
    font-size: 100%
}

section {
    height: 100vh;
    display: flex;
    scroll-snap-align: start;
    justify-content: center;
    align-items: center;
}

.snapcontainer {
    scroll-snap-type: y mandatory;
    overflow-y: scroll;
    height: 100vh;
    margin-top: 0;
}

a {
    font-size: 16pt;
}

h2 {
    font-size: 48px;
}

h3{
    font-size: 24px;
}

.headingcontainer {
    border-radius: 0;
    padding: 1.5% 10px;
    max-width: 75%;
    text-align: center;
    background-color: $opacityBackground;
}

.bodycontainer {
    border-radius: 0;
    padding: 1% 10px;
    max-width: fit-content;
}

.formcontainer {
    text-align: left;
}

.textrecolor {
    color: black;
}

.clickables {
    background-color: $clickableBackground;
    padding: 5px;
    margin-top: 5px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease 0s;
}

.clickables:hover {
    background-color: $clickHover;
}

table, td, th {
    border: 1px solid;
    padding: 2px;
  }
  
table {
    border-collapse: collapse;
  }

.center {
    text-align: center;
}

.opacity {
    opacity: 0.8;
}

.hidden{
    visibility: hidden;
}

.baseflex{
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    padding: 0 10%;
    width: 100%;
    height: 100px;
    background-color: $navBackground;
}

.navcontainer {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    padding: 0 10%;
    background-color: $navBackground;
}

.logo {
    display: flex;
    justify-content: flex-start;
    background-color: $navBackground;
    margin-right: 5px;
}

.logo h1 {
    background-color: $navBackground;
}

ul {
    background-color: $navBackground;
}

ul li {
    font-size: 18pt;
    background-color: $navBackground;
    list-style: none;
    display: inline-block;
    line-height: 100px;
}

ul li a {
    display: inline-block;
    transition: all 0.3s ease 0s;
    padding: 0 20px;
    background-color: $navBackground;
}

ul li a:hover{
    background-color: $clickHover;
}