* {
  text-decoration: none;
  margin: 0;
  padding: 0;
  font-family: "Prata", Arial, Helvetica, sans-serif;
  color: rgb(255, 255, 255);
}

html {
  background-image: url("monotoneBusiness1080.webp");
  background-size: cover;
}

body {
  font-size: 100%;
}

section {
  height: 100vh;
  display: flex;
  scroll-snap-align: start;
  justify-content: center;
  align-items: center;
}

.snapcontainer {
  -ms-scroll-snap-type: y mandatory;
      scroll-snap-type: y mandatory;
  overflow-y: scroll;
  height: 100vh;
  margin-top: 0;
}

a {
  font-size: 16pt;
}

h2 {
  font-size: 48px;
}

h3 {
  font-size: 24px;
}

.headingcontainer {
  border-radius: 0;
  padding: 1.5% 10px;
  max-width: 75%;
  text-align: center;
  background-color: rgba(44, 44, 44, 0.8);
}

.bodycontainer {
  border-radius: 0;
  padding: 1% 10px;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
}

.formcontainer {
  text-align: left;
}

.textrecolor {
  color: black;
}

.clickables {
  background-color: rgba(17, 17, 17, 0.8);
  padding: 5px;
  margin-top: 5px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease 0s;
}

.clickables:hover {
  background-color: rgb(32, 32, 32);
}

table, td, th {
  border: 1px solid;
  padding: 2px;
}

table {
  border-collapse: collapse;
}

.center {
  text-align: center;
}

.opacity {
  opacity: 0.8;
}

.hidden {
  visibility: hidden;
}

.baseflex {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  padding: 0 10%;
  width: 100%;
  height: 100px;
  background-color: #111111;
}

.navcontainer {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  background-color: #111111;
}

.logo {
  display: flex;
  justify-content: flex-start;
  background-color: #111111;
  margin-right: 5px;
}

.logo h1 {
  background-color: #111111;
}

ul {
  background-color: #111111;
}

ul li {
  font-size: 18pt;
  background-color: #111111;
  list-style: none;
  display: inline-block;
  line-height: 100px;
}

ul li a {
  display: inline-block;
  transition: all 0.3s ease 0s;
  padding: 0 20px;
  background-color: #111111;
}

ul li a:hover {
  background-color: rgb(32, 32, 32);
}/*# sourceMappingURL=index.css.map */